export const monitorAPI = {
  // 
  start: () => api.post('/monitor/start'),
  // 
  stop: () => api.post('/monitor/stop'),
  // 
  status: () => api.get('/monitor/status'),
  // 
  refresh: () => api.post('/monitor/refresh'),
  // 
  stats: () => api.get('/monitor/stats'),
  //  (Server-Sent Events)
  createLogStream: (onLog, onError) => {
    // CookieEventSourceCookie
    // URLsession_idCookie
    let url = '/api/v1/monitor/logs'
    
    // Cookiesession_id
    // Cookie
    const sessionId = document.cookie
      .split(';')
      .find(row => row.trim().startsWith('session_id='))
      ?.split('=')[1]
    
    if (sessionId) {
      url += `?session_id=${encodeURIComponent(sessionId)}`
    }
    
    const eventSource = new EventSource(url)
    
    eventSource.onmessage = (event) => {
      if (event.type === 'log') {
        try {
          const logData = JSON.parse(event.data)
          onLog && onLog(logData)
        } catch (error) {
          console.error(':', error)
        }
      }
    }
    
    eventSource.addEventListener('log', (event) => {
      try {
        const logData = JSON.parse(event.data)
        onLog && onLog(logData)
      } catch (error) {
        console.error(':', error)
      }
    })
    
    eventSource.addEventListener('heartbeat', (event) => {
      // 
      console.debug(':', event.data)
    })
    
    eventSource.onerror = (error) => {
      console.error(':', error)
      onError && onError(error)
    }
    
    return eventSource
  }
}

